		margin-top: 20px;
}
.form-check{
    	display: inline;
}
label{
	position: relative;
	cursor: pointer;
	color: #666;
	font-size: 30px;
}
input[type="checkbox"]{
	position: absolute;
	right: 9000px;
}

	/*Check box*/
input[type="checkbox"] + .label-text:before{
	content: "\F0C8";
	font-family: FontAwesome;
	speak: none;
	font-style: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	-webkit-font-smoothing:antialiased;
	width: 1em;
	display: inline-block;
	margin-right: 5px;
	color: #FFF;
}
input[type="checkbox"]:checked + .label-text:before{
	content: "\F14A";
	font-family: FontAwesome;
    
	color: #2980b9;
	-webkit-animation: effect 250ms ease-in;
	        animation: effect 250ms ease-in;
	font-weight: 900;
}
input[type="checkbox"]:disabled + .label-text{
	color: #aaa;
}
input[type="checkbox"]:disabled + .label-text:before{
	content: "\F0C8";
	font-family: FontAwesome;
	color: #ccc;
}
























